@media screen and (min-width: 320px){
    .mainDiv{
        margin:100px 0;
    }
    .title{
        height: 150px;
        background: #252525;
        display: flex;
        align-items: center;
    }
    .titleText{
        color: #FFF;
        font-family: 'Poppins';
        font-size: 35px;
        font-style: normal;
        font-weight: 700;
        line-height: 35px;
        margin-left: 10px;
    }
    .conClients{
        margin-bottom: 50px;
    }
    .titleClients{
        color: #FFF;
        font-family: 'Poppins';
        font-size: 23px;
        font-style: normal;
        font-weight: 700;
        line-height: 35px;
        margin-left: 37px;
    }
    .loremClients{
        color: rgba(255, 255, 255, 0.50);
        font-family: 'Poppins';
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: 35px;
        text-align: center;
    }
    .titleGallery{
        color: #FFF;
        font-family: 'Poppins';
        font-size: 23px;
        font-style: normal;
        font-weight: 700;
        line-height: 35px;
        margin-left: 37px;
    
    }
    .listProjects{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0;
    }
    .site{
        width: 222px;
        height: 125px;
        background: url("../images/WebStudio.png"), lightgray -44.962px -5px / 118.931% 101.852% no-repeat;
        background-size: contain;
        display: flex;
        justify-content: center;
        align-items: flex-end;
    }
    .site:nth-child(2){
        background: url("../images/Restoran.png"), lightgray -44.962px -5px / 118.931% 101.852% no-repeat;
        background-size: contain;
        margin: 20px 0;
    }
    .site:nth-child(3){
        background: url("../images/EventBuster.png"), lightgray -44.962px -5px / 118.931% 101.852% no-repeat;
        background-size: contain;
    }
    .BVSite{
        width: 163px;
        height: 30px;
        flex-shrink: 0;
        border-radius: 30px;
        border: 3px solid #D5D5D5;
        background: #333;
        color: #FFF;
        font-family: 'Poppins';
        font-size: 15px;
        font-style: normal;
        font-weight: 400;
        line-height: 10px;
        opacity: 0;
        transition: opacity 1s ease-in-out;
        margin-bottom: 17px;
    }
    .site:hover .BVSite{
        opacity: 1;
        border-radius: 30px;
        border: 3px solid #007CED;
        background: #333;
    }
}
@media screen and (min-width: 768px){
    .title{
        height: 200px;
    }
    .titleText{
        font-size: 45px;
    }
    .titleClients{
        font-size: 25px;
    }
    .loremClients{
        font-size: 17px;
    }
    .titleGallery{
        font-size: 25px;
    }
    .listProjects{
        display: flex;
        flex-direction: row;
        justify-content: center;
        padding: 0;
    }
    .site:nth-child(2){
        margin: 0 20px;
    }
    .BVSite{
        font-size: 17px;
    }
}
@media screen and (min-width: 1112px){
    .title{
        height: 229px;
    }
    .titleText{
        font-size: 55px;
    }
    .titleClients{
        font-size: 28px;
    }
    .loremClients{
        font-size: 20px;
    }
    .titleGallery{
        font-size: 28px;
    }
    .site{
        width: 443px;
        height: 250px;
    }
    .BVSite{
        font-size: 20px;
    }
}
